export default {
	props: {
		item: Object,
		index: {
			type:Number,
			default:-1
		},
		isDetail:{
			type:Boolean,
			default:false
		}
	},
	methods:{
		// 打开个人空间
		openSpace(){
			uni.navigateTo({
				url: '/pages/user-space/user-space',
			});
		},
		// 关注
		follow(){
			//通知父组件
			this.$emit('follow',this.index);
		},
		// 顶踩操作
		doSupport(type){
			this.$emit('doSupport',{
				type:type,
				index:this.index
			});
		},
		// 评论
		doComment(){
			if (!this.isDetail) {
				return this.openDetail()
			}
			this.$emit('doComment')
		},
		// 分享
		doShare(){
			if (!this.isDetail) {
				return this.openDetail()
			}
			this.$emit('doShare')
		},
		// 进入详情页
		openDetail(){
			// 如果处在详情页
			if (this.isDetail) return;
			uni.navigateTo({
				url: '../../pages/detail/detail?detail='+JSON.stringify(this.item),
			});
		}
	}
}
